import React,{useState} from 'react'
import arrowUp from '../../assets/arrowUp.svg'
import Post from '../Post'

const PostsResult = (props) => {
  const [onShowPosts, setOnShowPosts] = useState(false)

  return (
    <div>
        <div className='headit flexit' onClick={()=>{setOnShowPosts(!onShowPosts)}}>
            <h3 className='heading mince'>Posts</h3>
            <img src={arrowUp} className={`plus toRotate ${onShowPosts?("rotate"):("")}`} />
        </div>
        {onShowPosts && <div>
        {
            props.result.map((post)=>{
                return <Post name={post.userName} content={post.content} postID={post.postID} profile={props.profile}/>
            })
        }
        </div>}
        
    </div>
  )
}

//<Post name={post.userName} content={post.content} role={"Student"} postID={post.postID}/>

export default PostsResult