import React,{useState} from 'react'
import arrowUp from '../../assets/arrowUp.svg'
import profprofile from '../../assets/profprofile.png' 
import AddToContact from './AddToContact'

const PeopleResult = (props) => {
    const [onShowPeople, setOnShowPeople] = useState(true)
    return (
        <div >
            <div className='headit flexit' onClick={()=>{setOnShowPeople(!onShowPeople)}}>
                <h3 className='heading mince'>People</h3>
                <img src={arrowUp} className={`plus toRotate ${onShowPeople?("rotate"):("")}`}/>
            </div>
            {onShowPeople && 
                <div>
                {
                    props.result.map((contact)=>{
                        return <AddToContact result={contact} profile={profprofile}/>

                    })
                } 

            </div>
            }
        </div>
  )
}

export default PeopleResult