import React, { useEffect, useState } from 'react'
import '.././styles/AddToContact/addtocontactstyle.css'
import axios from 'axios'
import { Link } from 'react-router-dom'
const AddToContact = (props) => {
  axios.defaults.withCredentials=true
  const [profilePic, setProfilePic] = useState()
  const getProfileImage = async()=>{
    await axios.post(`/contact/contactprofileimage`,{userName:props.result.userName},{responseType: 'arraybuffer'}).then((resp)=>{
  
      let base64ImageString = Buffer.from(resp.data, 'binary').toString('base64')
      setProfilePic("data:image/png;base64,"+base64ImageString)
      
        }).catch((err)=>{
            console.log(err);
        })
    }
    const addContact = async()=>{
      await axios.post(`/contact/addcontact`,{contact2UserName:props.result.userName})
      .then((res)=>{
          console.log(res);
      }).catch((err)=>{
          console.log(err);
      })
  }
   useEffect(()=>{
    getProfileImage()
   },[]) 
  return (
    <div className='contain flexing'>
        <div className='lefted ppl-result'> 
            <img src={profilePic} />
            <div className='name-role ' >
                <h3 className='name '>{props.result.name}</h3>
                <p className='job'>{"Teacher"}</p>
            </div>
        </div>  
        {
          !props.result.iscontact?(
            <a className='btn-add-to' onClick={addContact}>Add to contact </a>    
          ):(
            <Link className='see-profile' to={`/profile/${props.result.userName}`}>See Profile</Link>    
          )
        }  
    </div>
  )
}

export default AddToContact