import React, { useRef } from 'react';
import axios from 'axios';
const DeletePost = (props) => {
  let deleteWarning = useRef();

  const deletePost = () => {
    axios
      .post('/post/deletepost', {
        postID: props.postID,
      })
      .then((res) => {
        props.postRef.current.style.display = 'none';
      });
  };
  return (
    <React.Fragment>
      <div
        onClick={() => {
          deleteWarning.current.style.display = 'flex';
        }}
      >
        <p>Delete Post</p>
      </div>
      <section className="delete-warning" ref={deleteWarning}>
        <p>Are you sure you want to delete the post ?</p>
        <div>
          <button onClick={deletePost}>Delete</button>
          <button
            onClick={() => {
              deleteWarning.current.style.display = 'none';
            }}
          >
            Cancel
          </button>
        </div>
      </section>
    </React.Fragment>
  );
};

export default DeletePost;
