import axios from 'axios';
import React, {
  useCallback,
  useEffect,
  useRef,
  useState,
} from 'react';
import reply from '../assets/reply.svg';
import upVotedBlack from '../assets/UpVotedBlack.svg';
import upVotedBlue from '../assets/UpVotedBlue.svg';
import { Link } from 'react-router-dom';
import DeletePost from './profileSubCompenents/DeletePost';

const SERVER = 'http://localhost:3001';

const Post = (props) => {
  axios.defaults.withCredentials = true;
  const upVoteimg = useRef(null);
  const upVotep = useRef(null);
  const [postImage, setPostImage] = useState();
  const [profileImg, setProfileImg] = useState(props.pic);
  const handleUpVote = () => {
    if (upVotep.current.textContent != 'Up voted') {
      upVoteimg.current.src = upVotedBlue;
      upVotep.current.textContent = 'Up voted';
      upVotep.current.style.color = '#1E53CF';
      console.log(upVotep.current);
    } else {
      upVoteimg.current.src = upVotedBlack;
      upVotep.current.textContent = 'Up vote';
      upVotep.current.style.color = '#000000';
      console.log(upVotep.current);
    }
  };

  const getProfileImage = async () => {
    await axios
      .post(
        `/post/getposterimageprofile`,
        { postID: props.postID },
        { responseType: 'arraybuffer' }
      )
      .then((resp) => {
        let base64ImageString = Buffer.from(
          resp.data,
          'binary'
        ).toString('base64');
        setProfileImg('data:image/png;base64,' + base64ImageString);
      })
      .catch((err) => {
        console.log(err);
      });
  };
  const getImage = async () => {
    await axios
      .post(
        `/post/getpostimage`,
        { postID: props.postID },
        { responseType: 'arraybuffer' }
      )
      .then((res) => {
        let base64ImageString = Buffer.from(
          res.data,
          'binary'
        ).toString('base64');
        setPostImage('data:image/png;base64,' + base64ImageString);
      });
  };
  const deletePost = () => {
    axios
      .post('/post/deletepost', {
        postID: props.postID,
      })
      .then((res) => {
        post.current.style.display = 'none';
      });
  };
  let options = {
    root: null,
    threshold: 1.0,
  };
  let post = useRef();
  const callBack = (entry) => {
    if (entry && entry.isIntersecting) {
      getProfileImage();
      getImage();
      observer.unobserve(post.current);
    }
  };
  let observer = new IntersectionObserver(([entery]) => {
    callBack(entery);
  }, options);

  useEffect(() => {
    observer.observe(post.current);
  }, []);

  return (
    <div className="post" ref={post}>
      <div>
        <div>
          <Link to={`/profile/${props.name}`}>
            <div>
              <img src={profileImg} alt="Profile" />
            </div>
            <div>
              <h5>{props.name}</h5>
              <p>{props.role ? 'Student' : 'Teacher'}</p>
            </div>
          </Link>
        </div>
        {props.isMyProfile ? (
          <DeletePost postRef={post} postID={props.postID} />
        ) : (
          <div onClick={handleUpVote}>
            <img src={upVotedBlack} alt="" ref={upVoteimg} />
            <p ref={upVotep}>Up vote</p>
          </div>
        )}
      </div>
      <div>
        <p>{props.content}</p>
      </div>
      <div>
        <img
          src={postImage}
          onError={(event) => (event.target.style.display = 'none')}
        />
      </div>
      <div>
        <Link to={`/post/${props.postID}`}>
          <p>See more</p>
        </Link>
      </div>
    </div>
  );
};

export default Post;
