import React,{useState} from 'react'
import Course from '../GlobalSubCompenents/Course'
import arrowUp from '../../assets/arrowUp.svg'

const CourseResult = (props) => {
    const [onShowCourses, setOnShowCourses] = useState(false)

    return (
        <div >
            <div className='headit flexit' onClick={()=>{setOnShowCourses(!onShowCourses)}}>
                <h3 className='heading mince'>Courses</h3>
                <img src={arrowUp} className={`plus toRotate ${onShowCourses?("rotate"):("")}`} />
            </div>
            {onShowCourses && <div> 
                {
                    props.result.map((course)=>{
                        return <Course userName={course.userName} name={course.userName} module={course.className} title={course.pdfName} text={course.content} />
                    })
                }
            </div>}
        </div>
    )
}
//<Course profile={profprofile} name={courseList.info.firstName+" "+courseList.info.lastName} module={props.module} title={course.pdfName} text={course.content}/>
export default CourseResult