import React from 'react'
import AddToContact from './searchSubCompenents/AddToContact'
import Course from './GlobalSubCompenents/Course'
import Post from './Post'
import './styles/Search/searchstyle.css'
import profprofile from '../assets/profprofile.png' 
import arrowDown from '../assets/arrowDown.svg' 
import arrowUp from '../assets/arrowUp.svg' 
import { useParams } from 'react-router-dom'

import {useEffect, useRef, useState} from 'react'
import search_icon from "../assets/search_icon.svg"
import notification_icon from "../assets/notification_icon.svg"
import './styles/Header.js/headerstyle.css'
import modeToggle from '../assets/DarkToggle.svg'
import { useSelector } from 'react-redux'
import SearchContact from './headersubcompenent/SearchContact'
import axios from 'axios'
import { Link } from 'react-router-dom'
import PeopleResult from './searchSubCompenents/PeopleResult'
import PostsResult from './searchSubCompenents/PostsResult'
import CourseResult from './searchSubCompenents/CourseResult'


const Search = () => {

  const [onShowPosts, setOnShowPosts] = useState(false)

  let [searchResult,setSearchResult]=useState({
    people:[],
    posts:[],
    course:[]
  });
  let temp = useParams().searched
  const searched = useRef(useParams().searched)
  useEffect(() => {
    searched.current=temp
  }, [useParams()])
  

  let result;
  const handleSearch = async()=>{
        setSearchResult([])
        await axios.get(`/search/search`,{
          params:{
            serched: searched.current
          }
        }).
        then((res)=>{
          setSearchResult(res.data.result)})
    }
    useEffect(() => {
      handleSearch()
    }, [useParams()])
    useEffect(() => {
      handleSearch()
    }, [])
  
  return (
    <div className='b-search'>
        <h5 className='heading'>Search result of {searched.current}</h5>
        <div className='results'>
        {
          searchResult.people && <PeopleResult result={searchResult.people} />
        }
        {
          searchResult.posts && <PostsResult result={searchResult.posts} profile={profprofile} />
        } 
        {
          searchResult.course && <CourseResult result={searchResult.course} profile={profprofile} />
        }
        </div>
    </div>
  )
}

export default Search